import { useRouter } from "next/router";

const categoryContent = {
  history: "Контент по истории...",
  "pedagogy-general": "Общая информация о педагогике...",
  "pedagogy-languages": "Преподавание языков...",
  "pedagogy-sport": "Физическая культура...",
  law: "Правовые материалы...",
  "sociology-politics": "Социология и политология...",
  philology: "Филология...",
  philosophy: "Философия...",
};

export default function EditorialCategory() {
  const router = useRouter();
  const { category } = router.query;
  const content = categoryContent[category] || "Категория не найдена.";

  return (
    <div className="max-w-4xl mx-auto p-6">
      <h1 className="text-2xl font-bold">Редакция: {category}</h1>
      <p className="mt-4">{content}</p>
    </div>
  );
}
